import Vue from 'vue';
import Vuex from 'vuex';


Vue.use(Vuex);


const store =new Vuex.Store({
    state:{
        active:0
    },
    mutations:{
      add(state){
        state.active++;
        console.log(state.active)
      },
         
    },
    
    
    actions: {
        increment(context) {
            context.commit("add");
        },
        decrement(context) {
            context.commit("DECREMENT");
        }
    }
})

export default store


